.nav {
  &__item {
    margin-bottom: 32px;
  }

  &__link {
    position: absolute;
    display: inline-block;
    color: $main-text-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &:hover {
      content: "";
      display: block;
      width: fit-content;
      padding-bottom: 7px;
      border-bottom: 1px solid $main-text-color;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      display: block;
      width: 100%;
      height: 1px;
      background-color: $main-text-color;

      transition: transform 0.3s;
      transform: scale(0);
    }

    &hover::after {
      transform: scale(1);
    }
  }

  &__list {
    position: relative;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}
