.data-form {
  @include onTablet {
    width: 288px;
    height: 382px;

    padding-right: 24px;
  }

  @include onDesktop {
    width: 411px;
    height: 382px;

    padding-right: 198px;
  }

  &__button {
    margin-top: 32px;

    @include onTablet {
      width: 288px;
    }

    @include onDesktop {
      width: 411px;
    }
  }
}

.data {
  padding-right: 20px;

  &__input {
    padding-top: 16px;
  }
}

.figure {
  margin-bottom: 16px;
  padding-left: 16px;
  width: 100%;
  height: 48px;
  border-radius: 40px;

  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 140%;

  &:hover {
    box-shadow: 1px 4px 16px 0 #e8e8f066;
  }

  &:active {
    box-shadow: 1px 4px 16px 0 #e8e8f0a3;
  }
}

.figure2 {
  padding-top: 14px;
  padding-left: 16px;
  width: 100%;
  height: 148px;
  border-radius: 16px;
  resize: none;

  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 140%;

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 50px;
  }

  &::-webkit-scrollbar-thumb {
    background: #1e1e1e;
    border-radius: 50px;
  }

  &:hover {
    box-shadow: 1px 4px 16px 0 #e8e8f066;
  }

  &:active {
    box-shadow: 1px 4px 16px 0 #e8e8f0a3;
  }
}
