.button {
  background-color: #fff;
  width: 100%;
  height: 56px;
  border-radius: 40px;
  color: $main-text-color;

  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0;

  &:hover {
    box-shadow: 1px 4px 16px 0 #e8e8f066;
  }

  &:active {
    box-shadow: 1px 4px 16px 0 #e8e8f0a3;
  }

  &__explore {
    @include onTablet {
      width: 392px;
      grid-column: 3 / 5;
    }

    @include onDesktop {
      width: 324px;
      grid-column: 5 / 9;
    }
  }
}
