.icon {
  display: block;
  background-size: contain;

  @include hover(transform, scale(1.2));

  &--phone {
    background-image: url("../images/Phone.svg");
    background-repeat: no-repeat;
    height: 18px;
    width: 18px;

    @include onTablet {
      height: 24px;
      width: 24px;
    }

    @include onDesktop {
      &::before {
        content: "+1 234 5555-55-55";
        color: #fff;
        font-weight: 700;
        font-size: 12px;
        width: max-content;
        position: absolute;
        right: 150%;
        top: 10%;
        height: 100%;
      }
    }
  }

  &--menu {
    background-image: url("../images/Menu.svg");
    background-repeat: no-repeat;
    height: 12px;
    width: 18px;

    @include onTablet {
      height: 16px;
      width: 24px;
    }

    &:hover {
      transform: scale(1.2);
      background-image: url("../images/Menu_active.svg");
    }
  }

  &--close {
    background-image: url("../images/Close.svg");
    background-repeat: no-repeat;
    height: 16px;
    width: 16px;

    @include onTablet {
      height: 22px;
      width: 22px;
    }
  }
}
